body {
  background-color: #52d8d1;
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
}

.container {
  max-width: 800px;
  margin: 20px auto;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  color: #333;
}

.registration-form {
  margin-bottom: 20px;
}

.registration-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  box-sizing: border-box;
}

.registration-form button {
  background-color: #d02721;
  color: white;
  padding: 10px 15px;
  border: none;
  cursor: pointer;
}

.table-container {
  margin-top: 20px;
}

.table {
  width: 100%;
  border-collapse: collapse;
}


.table th {
  background-color: #0071ce;
  color: white;
  padding: 10px;
  text-align: left;
}

.table tr {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 5px;
  overflow: hidden;
  transition: transform 0.2s ease;
}

.table tr:hover {
  background-color: #34bea7;
  transform: scale(1.02);
}

.table td {
  padding: 10px;
}


.pagination-container {
 display: flex;
 justify-content: center;
 margin-top: 20px;
}

.pagination-item .active {
 background-color: #4CAF50;
 color: white;
}

.pagination-item a {
 color: black;
 float: left;
 padding: 8px 16px;
 text-decoration: none;
 transition: background-color .3s;
 border: 1px solid #ddd;
 margin: 0 4px;
}

.pagination-item a.active {
 background-color: #4CAF50;
 color: white;
 border: 1px solid #4CAF50;
}

.pagination-item a:hover:not(.active) {
 background-color: #ddd;
}


/* Delete button styles */
.delete-btn {
  background-color: #d9534f; /* Bootstrap red color */
  color: white;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
  margin-right: 8px; /* Add margin-right for space */
}

.delete-btn:hover {
  background-color: #c9302c; /* Darker red on hover */
}

/* Update button styles */
.update-btn {
  background-color: #d9534f; /* Bootstrap red color */
  color: white;
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}

.update-btn:hover {
  background-color: #c9302c; /* Darker red on hover */
}
